import { Injectable } from '@angular/core';
import {Observable, Subject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EventManagerService {
  private testSubject: Subject<any> = new Subject<any>();

  constructor() {
    this.getObservable().subscribe((t) => {this.doAlert(t);});
  }

  public doAlert(thing: any){
    alert(`WARNING ${thing} clouds forecast`);
  }
  public getObservable(): Observable<any>{
    return this.testSubject.asObservable();
  }


  public getSubject(): Subject<any>{
    return this.testSubject;
  }


}
